.header {
  z-index: 4;
  padding-bottom: 60px;
}

@media screen and (min-width: 768px) {
  .header {
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1280px) {
  .header {
    padding-bottom: 97px;
  }
}

.header__container {
  justify-content: space-between;
  align-items: center;
